.login{
   height: 100vh;
   background-position: center;
   background-size: cover;
   display: flex;
   justify-content: center;
   align-items: center;
    .lgbg{
        width: 1200px;
        height: 720px;
        .part{
            width: 364px;
            height: 580px;
            padding: 140px 20px 0 20px;
            background-color: white;
            margin-left: auto;
            .title{
                margin-bottom: 50px;
                .logo{
                   display: flex;
                   justify-content: center;
                   align-items: center;
                }
                h1{
                    text-align: center;
                    color: #027cc5;
                    margin-top: 40px;
                }
            }
        }
    }
}